.commands{
    width: 100%;
    position: relative;
    justify-content: center;
    justify-items: center;
    align-items: center;
    text-align: center;
    display: flex;
    margin-top: 6%;
}

.Cmd-Header{
    margin-bottom: 10px;
}

.container1{
    justify-content: center;
    justify-items: center;
    align-items: center;
    text-align: left;
    color: white;
    width: 45%;
    background-color: #23272A;
    border-radius: 50px;
}

.accordion {
    padding: 40px;
    border-radius: 2px;
}

.accordion__item {
    border-bottom: 1px solid #7289DA;
}

.accordion__button {
    background-color: transparent;
    color: white;
    cursor: pointer;
    padding: 18px;
    width: 100%;
    text-align: left;
    border: none;
}

.accordion__button:hover {
    background-color: transparent;
}

.accordion__button:before {
    display: inline-block;
    content: '';
    height: 10px;
    width: 10px;
    margin-right: 12px;
    border-bottom: 2px solid currentColor;
    border-right: 2px solid currentColor;
    transform: rotate(-45deg);
}

.accordion__button[aria-expanded='true']::before,
.accordion__button[aria-selected='true']::before {
    transform: rotate(45deg);
}

[hidden] {
    display: none;
}

.accordion__panel {
    padding: 20px;
    animation: fadein 0.35s ease-in;
}

/* -------------------------------------------------- */
/* ---------------- Animation part ------------------ */
/* -------------------------------------------------- */

@keyframes fadein {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@media screen and (max-width: 1300px){
    .commands{
        margin-top: 10%;
    }
    .container1{
        width: 70%;
    }
}

@media screen and (max-width: 900px){
    .commands{
        margin-top: 20%;
        margin-bottom: 5%;
    }
    .container1{
        width: 90%;
    }
}
