.HomePage{
    width: 100%;
    position: relative;
    justify-content: center;
    justify-items: center;
    align-items: center;
    text-align: center;
    display: flex;
    margin-top: 10%;
}

.GreenBox{
    position: absolute;
    z-index: 1;
    width: 55%;
    margin-top: -0%;
    height: 150px;
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
    background: green;
    color: green;
}

.HomePage .Box{
    z-index: 1;
    width: 55%;
    background-color: #23272A;
    border-radius: 50px;
    margin-bottom: 8%;
}

.HomePage .Box .Main{
    margin-top: 2%;
    position: relative;
    margin-left: 5%;
    justify-content: left;
    display: grid;
    grid-template-columns: 250px 300px 300px;
}

.Main .img {
    margin-top: 10%;
    width: 200px;
    padding: 20px;
    z-index: 3;
    background: #23272A;
    border-radius: 50%;
}

.Main .img-balance{
    width: 70px;
    padding: 10px;
    z-index: 3;
    margin-top: 45%;
    margin-left: 100%;
}

.Main .content{
    color: white;
    margin-top: 30%;
    z-index: 3;
}

.Main .content .buttons{
    position: relative;
    display: inline-flex;
    margin-top: 8%;
}

.Main .content .buttons .button{
    padding: 5px;
    padding-left: 20px;
    padding-right: 20px;
    background: #7289DA;
    border-radius: 30px;
    font-size: 24px;
}

.Main .content .buttons .button:hover{
    background: #647cd1;
}

.button a {
    color: white;
    text-decoration: none;
    
}

.HomePage .Box .second{
    text-align: left;
    margin-top: 6%;
    margin-bottom: 7%;
    color: white;
}

.HomePage .Box .second h2{
    margin-left: 5%;
}

.HomePage .Box .stats{
    position: relative;
    display: grid;
    grid-template-columns: auto auto auto auto;
    margin-bottom: 7%;
    color: white;
}

@media screen and (max-width: 1440px){
    .Main .img-balance{
        margin-left: 30%;
    }
}

@media screen and (max-width: 1300px){
    .Main .img-balance{
        margin-left: 0%;
    }
    .HomePage .Box{
        margin-bottom: 20%;
    }
}

@media screen and (max-width: 1124px){

    .HomePage .Box .Main{
        grid-template-columns: 220px 250px 300px;
    }

    .Main .img-balance{
        margin-left: -3%;
    }

    .Main .content{
        margin-top: 40%;
    }
}

@media screen and (max-width: 900px){
    .HomePage .Box{
        width: 80%;
    }
    
    .GreenBox{
        width: 80%;
    }
}

@media screen and (max-width: 500px){
    .HomePage .Box{
        width: 80%;
        margin-top: 10%;
    }
    
    .GreenBox{
        width: 80%;
        height: 100px;
    }
    
    .HomePage .Box .Main {
        margin-top: 5%;
        display: grid;
        grid-template-columns: none;
        grid-template-rows: 100px;
    }

    .HomePage .Box .Main .content h1{
        font-size: 18px;
    }

    .HomePage .Box .Main .content .buttons .button a {
        font-size: 18px;
    }


    .HomePage .Box .second{
        margin-top: 12%;
        margin-bottom: 10%;
    }

    .HomePage .Box .second h2{
        font-size: 15px;
    }

    .HomePage .Box .stats{
        margin-bottom: 10%;
    }

    .HomePage .Box .stats h2 {
        font-size: 15px;
    }

    .Main .img {
        width: 120px;
        padding: 20px;
        z-index: 3;
        background: #23272A;
        border-radius: 50%;
    }

    .Main .img-balance{
        position: absolute;
        margin-left: 70%;
        margin-top: 25%;
    }
    
}