.Info-Page{
    width: 100%;
    position: relative;
    justify-content: center;
    justify-items: center;
    align-items: center;
    text-align: center;
    display: flex;
    margin-top: 15%;
    color: white;
    margin-bottom: 18%;
}

.Info-Box {
    display: flex;
    width: 90%;
}

.Info-Box .Box {
    width: 30%;
    margin-left: 5%;
    background-color: #23272A;
    border-radius: 30px;
}   

.Info-Box .Box1 {
    width: 30%;
    background-color: #23272A;
    border-radius: 30px;
}

.Info-Box .Box h2 {
    margin-top: 30px;
    margin-bottom: 30px;
}

.Info-Box .Box1 h2 {
    margin-top: 30px;
    margin-bottom: 30px;
}

.Box-Icon {
    margin-top: 8%;
}

.Info-Box .Box1 svg {
    width: 80px;
    height: 80px;
}

.Info-Box .Box svg {
    width: 80px;
    height: 80px;
}

.Info-Text{
    display: flex;
    padding-left: 40px;
    padding-right: 40px;
    margin-bottom: 8%;
}