.footer {
    background: #23272A;
    background-color: #23272A;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: relative;
    margin-top: 2%;
    left:0;
    bottom:0;
    right:0;
    width: 100%;
    z-index: 999;
}

.footer-container {
    text-align: center;
    display: grid;
    height: 60px;
    background: #23272A;
  }
  
  .container {
    z-index: 1;
    width: 100%;
    max-width: 1300px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 50px;
    padding-left: 50px;
    justify-content: center;
    background: #23272A;
  }

.container p{
    font-family: consolas;
    margin-top: 1%;
    color:white;
}

.MyLink{
    color: #7289DA;
    text-decoration: none;
}